<template>
    <v-container
            fluid
            style="max-width:600px"
    >
        <v-row no-gutters>
            <v-col>
                <v-card class="mx-auto ma-5 pt-12">
                    <v-img
                        class="white--text mx-auto my-auto"
                        width="400"
                        src="/img/000. Rolling Stone Magazine's 500 Greatest Songs Of All Time1.jpg"
                    ></v-img>

                    <v-card-title class="title font-italic font-weight-light">
                        Rolling Stone Magazine's 500 greatest songs of all time...
                    </v-card-title>
                    <v-card-text class="mb-2">
                        ref : <a href="https://archive.org/details/RollingStoneMagazines500GreatestSongsOfAllTime..." target="_blank">https://archive.org</a>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>

        <v-row no-gutters>
            <v-col>
                <v-expansion-panels
                        class="mx-auto mb-2"
                >
                    <v-expansion-panel>
                        <v-expansion-panel-header v-slot="{ open }">
                            <v-row no-gutters>
                                <v-col cols="4">Setting</v-col>
                                <v-col class="text--secondary">
                                    <v-fade-transition leave-absolute>
                                        <span v-if="open" key="0">
                                            Default : 10s / Current : {{setting.interval}}s
                                        </span>
                                        <span v-else key="1">
                                            Time Interval : {{setting.interval}}s
                                        </span>
                                    </v-fade-transition>
                                </v-col>
                            </v-row>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-row no-gutters>
                                <v-col>
                                    <v-text-field
                                        label="Time Interval"
                                        suffix="s"
                                        v-mask="customMask"
                                        value="3"
                                        v-model="setting.userInterval"
                                        :disabled="progress.start"
                                        :rules="setting.rules"
                                    ></v-text-field>
                                </v-col>
                                <v-divider
                                        vertical
                                        class="mx-4"
                                ></v-divider>
                                <v-col>
                                    <v-text-field
                                            label="Current Line"
                                            :value="line.current"
                                            v-model="line.current"
                                            :rules="line.rules"
                                    ></v-text-field>
                                </v-col>
                            </v-row>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="cancel"
                                >
                                    Default
                                </v-btn>
                                <v-btn
                                    text
                                    color="primary"
                                    @click="save"
                                >
                                    Save
                                </v-btn>
                            </v-card-actions>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-col>
        </v-row>

        <v-row>
            <v-col>
                {{ down.fileName }}
                <v-progress-linear
                        :active="active"
                        :color="progress.color"
                        v-model="progress.value"
                        height="25"
                        reactive
                >
                    <strong>{{ progress.value }}%</strong>
                </v-progress-linear>
            </v-col>
        </v-row>
        <v-row>
            <v-col class="mr-1">
                <v-btn block color="blue-grey" dark @click="start">{{ down.state }}</v-btn>
            </v-col>
            <v-col class="ml-1">
                <v-btn block color="deep-orange darken-1" dark v-if="progress.start" @click="stop">Stop</v-btn>
                <v-btn block color="teal darken-1" dark v-else @click="init">Initialize (Set List)</v-btn>
            </v-col>
        </v-row>
        <v-row>
            <v-col class="mr-1">
                <v-alert
                        border="top"
                        colored-border
                        type="info"
                        elevation="2"
                >
                    <ul>
                        <li>When you click the button, start download on background. It may look like stopped, but it's working.</li>
                        <li>Total File Size : 4.28 GB.</li>
                        <li>If you use chrome browser, check your <a href="https://support.google.com/chrome/answer/114662" target="_blank">automaticDownloads</a> config.</li>
                    </ul>
                </v-alert>
            </v-col>
        </v-row>

        <v-row>
            <v-col>
                <v-btn
                    block
                    color="primary"
                    @click="expand = !expand"
                >
                    List of All (500 Lists)
                </v-btn>
            </v-col>
        </v-row>
        <v-row no-gutters>
            <v-col>
