                </v-expand-transition>
            </v-col>
        </v-row>
        <iframe id="download_iframe" style="display:none;"></iframe>
    </v-container>
</template>

<script>
    /* eslint-disable */
    import { mask } from 'vue-the-mask'

    const DEFAULT_INTERVAL = 10;

    export default {
        directives: {
            mask,
        },
        data () {
            // MARK: data
            return {
                expand: false,
                active: false,
                progress : {
                    start : false,
                    value : 0,
                    color : "blue darken-2"
                },
                customMask : {
                    mask: 'ZX',
                    tokens: {
                        'Z': {pattern: /[1-9]/},
                        'X': {pattern: /[0-9]/}
                    }
                },
                setting : {
                    interval : DEFAULT_INTERVAL,
                    userInterval : DEFAULT_INTERVAL,
                    rules : [
                        v => v >= 3 || 'Interval value must be more than 3',
                    ],
                },

                down : {
                    state : "Download",
                    fileName: "",
                    list: null,
                    loop: null,
                },

                line : {
                    current: 0,
                    rules : [
                        v => v >= 0 || 'line value must be more than 0',
                    ],
                    max: 0,
                }
            }
        },
        methods: {
            getList() {
                this.down.list = this.$el.querySelector('#download_list');
                this.line.max = this.down.list.value.split("\n").length;
            },
            init() {
                this.getList();
                this.down.state = 'Download';
                this.expand = false;
                this.active = false;
                this.progress.value = 0;
                this.setting.interval = DEFAULT_INTERVAL;
                this.setting.userInterval = DEFAULT_INTERVAL;

                this.line.current = 0;
                this.down.fileName = null;

                clearInterval(this.down.loop);
            },
            cancel() {
                this.setting.interval = DEFAULT_INTERVAL;
                this.setting.userInterval = DEFAULT_INTERVAL;
            },
            save() {
                this.setting.interval = this.setting.userInterval;
            },
            start() {
                if(this.progress.value >= 100) {
                    this.init();
                }

                this.down.state = 'Working..';
                this.active = true;
                if(this.progress.start === false) {
                    this.progress.start = true;
                    this.progress.color = "blue darken-2";
                }

                this.download(this.down.list.value.split("\n")[this.line.current]);
                clearInterval(this.down.loop);
                this.down.loop = setInterval(() => {
                    this.line.current++;
                    if(this.line.current >= this.line.max) {
                        this.finish();
                        return false;
                    }
                    this.download(this.down.list.value.split("\n")[this.line.current]);
                },this.setting.interval * 1000);
            },
            stop() {
                clearInterval(this.down.loop);
                this.down.state = 'Resume';
                this.progress.start = false;
                this.progress.color = "red darken-2";
            },
            finish() {
                clearInterval(this.down.loop);
                this.down.state = 'Done!';
                this.progress.value = 100;
                this.progress.start = false;
                this.progress.color = "green darken-1";
            },
            download(url) {
                this.active = true;
                this.progress.value = ((this.line.current / this.line.max) * 100).toFixed(1);
                
                let fileName = '';

                try {
                    let regex = new RegExp(/[^/\\&\?]+\.\w{3,4}(?=([\?&].*$|$))/, "g");
                    fileName = decodeURI(regex.exec(url)[0]).replace(/\s\s/gi, "");
                } catch (e) {
                    fileName = '';
                }

                if (fileName) {
                    this.down.fileName = fileName;
                } else {
                    this.down.fileName = 'unknown (skip)';
                    return false;
                }                

                this.axios({
                  url: url,
                    method: 'GET',
                    responseType: 'blob', // important
                }).then((response) => {
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', fileName); //or any other extension
                    document.body.appendChild(link);
                    link.click();
                });
            }
        },
        mounted : function() {
            this.init();
        },
        name: "rolling-stone-magazines500-greatest-songs-of-all-time.vue"
    }
</script>

